import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import {FlashMessagesService } from 'angular2-flash-messages'; 
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
username : String;
password : String;
  constructor(public authService : AuthService, public router : Router, public flashMessages : FlashMessagesService) { }

  ngOnInit() {
  }
  onLogin(){
    const user = {
      username : this.username,
      password : this.password
    }
    this.authService.authenticateUser(user).subscribe(data => {
      if(data.success){
              this.authService.storeUserData(data.token, data.user);
              this.flashMessages.show("You are now logged in!!", {cssClass : "alert-success", timeout : 1500});
              this.router.navigate(['/dashboard']);
      }else{
        this.flashMessages.show(data.msg, {cssClass : "alert-danger", timeout : 1500});
              this.router.navigate(['/login']);
      }
    })
  }
}



